.edit {
  place-items: center;
}

input, textarea, label {
  font-size: 1rem;
  padding: .5rem;
  display: block;
  margin-bottom: 3rem;
}


.checkboxes {
  display: grid;
  grid-template-columns: 1fr 4fr;
}

textarea {
  min-width: 50vw;
  min-height: 20vh;
}

.project-images {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 3rem;
}

.project-images > div {
  position: relative;
  display: grid;

}

.project-images img {
  max-height: 250px;
  width: 100%;
  object-fit: cover;
  grid-gap: 1rem;
  gap: 1rem;
}

.imageNameIndicator {
  text-transform: uppercase;
  text-align: center;
  font-size: 1rem;
  padding: 0 2rem;
}

select {
  margin-bottom: 3rem;
}

.edit img {
  width: 20rem;
  padding: 2rem;
}

.edit label {
  padding: 0;
}

.uploadButton {
  width: 120px;
  height: 30px;
  font-family: 'Josefin sans';
  text-transform: uppercase;
  padding: 10px;
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: .6rem;
  border: 1px solid #fff;
  border-radius: 25px;
  box-shadow: rgba(0, 0, 0, .2) 1px 1px 2px;
  margin-top: 1rem;
}
.project-details {
  display: grid;
  grid-auto-flow: row;
  padding-top: 10rem;
}

section {
  display: grid;
  margin-bottom: 5rem;
}

.top-info-section {
  padding: 0 15rem;
}

.project-details .project-details-content {
 display: grid;
 grid-gap: 2rem;
 gap: 2rem;
}

.project-details .project-details-content .edit-button {
  position: absolute;
  font-size: 1.5rem;
  top: 25vh;
  left: 10vw;
  -webkit-transition: .3s ease;
  transition: .3s ease;
  opacity: 0.5;
}

.project-details .project-details-content .back-button {
  position: absolute;
  font-size: 1.5rem;
  top: 15vh;
  left: 10vw;
  -webkit-transition: .3s ease;
  transition: .3s ease;
  opacity: 0.5;
}

.project-details .project-details-content .back-button:hover {
  position: absolute;
  top: 15vh;
  left: 10vw;
  opacity: 1;
  -webkit-transform: translateX(-2px);
          transform: translateX(-2px);
}

.project-details .project-details-content .info-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.project-details .project-details-content .project-detail-title {
  font-size: 4rem;
  font-family: 'eskapade-fraktur', serif;
  font-family: 'Yeseva One', cursive;
  margin-bottom: 7rem;
  -webkit-animation: fadeIn 1.5s ease .5s;
          animation: fadeIn 1.5s ease .5s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}

.project-details-content .description {
  width: 100%;
  padding-right: 2rem;
  font-family: "Eczar", serif;
  font-size: 1.3rem;
  -webkit-animation: fadeIn 1.5s ease .75s;
          animation: fadeIn 1.5s ease .75s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}

.project-details-content .project-list-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: end;
  grid-gap: 1rem;
  gap: 1rem;
  border-left: 1px solid #fff;
  padding-left: 3rem;
  -webkit-animation: fadeIn 1.5s ease .75s;
          animation: fadeIn 1.5s ease .75s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}

.project-details-content .project-list-info .list-info-items {
 justify-content: end;
}

.project-details-content .project-list-info .list-info-items .tagName {
  font-family: 'Yeseva One', cursive;
  font-size: 1rem;
  font-weight: 300;
}

.project-details .project-details-img-intro {
  box-shadow:  none;
  width: 70%;
  justify-self: center;
  -webkit-animation: fadeIn 1.5s ease 1s;
          animation: fadeIn 1.5s ease 1s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}

.project-details .project-details-img-result {
  box-shadow:  none;
  width: 100%;
  -webkit-animation: fadeIn 1.5s ease 1s;
          animation: fadeIn 1.5s ease 1s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}

.project-details .project-details-content .concept-section, .target-audience-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 0 15rem;
  grid-gap: 1rem;
  gap: 1rem;
}


.project-details .project-details-content .concept-section .concept-description .concept-title {
  font-size: 4rem;
  font-family: 'Yeseva One', cursive;
  text-align: left;
  margin-bottom: 3rem;
}

.project-details .project-details-content .concept-section .concept-name .concept-title {
  font-size: 3rem;
  font-family: 'Eczar', serif;
  text-align: left;
}

.project-details .project-details-content .concept-section .concept-description, .concept-name {
  font-size: 1.3rem;
  margin-top: 10rem;
  margin-bottom: 7rem;
  font-family: "Eczar", serif;
  text-align: left;
  justify-self: left;
  padding-right: 5rem;
  grid-column: 1 / 2;
}

.project-details .project-details-content .concept-section .concept-name {
  grid-column: 2 / 3;
  border-left: 1px solid #fff;
  padding-left: 3rem;
}

.pager {
  background: #161616;
  text-decoration: none;
  width: 100vw;
  height: 10vh;
  margin-top: 10vh;
  display: grid;
  grid-auto-flow: column;
  z-index: 5;
}

.pager .navigate-projects {
  display: grid;
  grid-auto-flow: column;
  place-content: end;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 4rem;
}

.navigate-projects .prev {
  margin-left: 4rem;
  font-size: .9rem;
  justify-self: end;
  opacity: 0.5;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.navigate-projects .divider {
  opacity: 0.5;
}

.navigate-projects .next {
  margin-right: 4rem;
  font-size: .9rem;
  justify-self: end;
  opacity: 0.5;
  -webkit-transition: .3s ease;
  transition: .3s ease;
}

.navigate-projects .prev:hover {
  opacity: 1;
  -webkit-transform: translateX(-2px);
          transform: translateX(-2px);
}

.navigate-projects .next:hover {
  opacity: 1;
  -webkit-transform: translateX(2px);
          transform: translateX(2px);
}

@media (max-width: 900px) {
  .project-details {
    justify-content: center;
    padding: 0;
  }

  section {
    margin-bottom: 3rem;
  }

  .top-info-section {
    padding: 0;
  }
  .project-details-content .description {
    margin-bottom: 3rem;
  }

  .project-details .project-details-content .project-detail-title {
    font-size: 3rem;
    padding-left: 2rem;
    margin-top: 7rem;
    margin-bottom: 3rem;
  }

  .project-details .project-details-content .info-grid {
    display: block;
    padding-left: 2rem;
    padding-right: 1rem;
    margin-bottom: 3rem;
  }

  .project-details .project-details-img-intro {
    width: 100%;
  }

  .project-details .project-details-content .concept-section {
    padding: 0;
  }

  .project-details .project-details-content .concept-section .concept-description {
    font-size: 1.2rem;
    line-height: 1.8rem;
    margin-top: 2rem;
    font-family: "Eczar", serif;
    padding: 2rem;
    text-align: left;
    grid-column: 1 / 3;
  }
  
  .project-details .project-details-content .concept-section .concept-description, .concept-name {
    padding-right: 2rem;
    margin-bottom: 1rem;
    grid-column: 1 / 3;
  }

  .project-details .project-details-content .concept-section .concept-name {
    grid-column: 1 / 3;
    border-left: 1px solid #fff;
    padding-left: 2rem;
    margin-left: 2rem;
    margin-top: 0;
  }

  .pager {
    justify-content: center;
  }

  .pager .navigate-projects {
    display: grid;
    grid-auto-flow: column;
    place-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
  }
  
  .navigate-projects .prev {
    margin-left: 4rem;
    font-size: .9rem;
    justify-self: center;
    opacity: 0.5;
    -webkit-transition: .3s ease;
    transition: .3s ease;
  }
  
  .navigate-projects .divider {
    opacity: 0.5;
  }
  
  .navigate-projects .next {
    margin-right: 4rem;
    font-size: .9rem;
    justify-self: center;
    opacity: 0.5;
    -webkit-transition: .3s ease;
    transition: .3s ease;
  }
} 
